@mixin toggler() {
  position: absolute;
  width: 50px;
  height: 50px;
  border: none;
  cursor: pointer;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 5;
  background-color: transparent;
  background-color: rgb(34, 34, 34);
  border-radius: 50%;
  box-shadow:
    2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
    100px 100px 80px rgba(0, 0, 0, 0.07);

  &:before,
  &:after {
    content: '';
    display: block;
    position: absolute;
    background-image: url(~@img/chat.png);
    background-size: cover;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 50%;
    height: 50%;
    transition: opacity 500ms;
    opacity: 0.5;
    z-index: 2;
  }

}

.chat-block {
  background-color: rgb(38, 38, 40);
  display: flex;
  flex-direction: column;
  position: relative;

  &__login {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    box-shadow: 19px -1px 30px -17px rgba(0, 0, 0, 0.5) inset;
  }

  &__toggler {
    @include toggler();

  }

  &__toggler:hover::before {
    opacity: 0.75;
  }

  &__rotation-lock {
    @include toggler();
    bottom: 90px;
    transition: background-color 500ms, opacity 500ms;

    &:before {
      opacity: 0.5;
      background-image: url(~@img/rotation-lock.png);
    }

    &:after {
      opacity: 0.5;
      transition: opacity 500ms, transform 500ms;
      background-image: url(~@img/rotation-lock-n.png);

    }

  }

  &__rotation-lock--active {
    background-color: gray;
    opacity: 0.5;

    &:before {
      opacity: 0;
    }

    &:after {
      transform: rotate(-180deg)
    }

    &:hover {
      opacity: 1;
    }
  }






  &__header {
    flex: none;
    padding: 20px;
    box-shadow: -1px 3px 27px 0px rgba(0, 0, 0, 0.75);
  }




  &__body {
    flex: 1;
    overflow: auto;

    @include custom-scroll();
  }

  &__body-inner {
    padding: 20px 20px;

  }

  &__footer {
    flex: none;
    padding: 0 20px;
    box-shadow: 1px -7px 19px -4px rgba(0, 0, 0, 0.35);
  }

  &__author {
    height: 50px;
  }


  &--active &__content {
    display: none;
  }

}

.user {
  display: flex;
  align-items: center;

  &__avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    transition: opacity 500ms;
    opacity: 0.5;

    img {
      width: 100%;
    }
  }

  &__avatar:hover {
    opacity: 0.75;
  }

  &__avatar+&__name {
    margin-left: 20px;
  }

  &__name {
    font-size: 1rem;
    color: transparentize($color: $white, $amount: 0.5);
    user-select: none;
    pointer-events: none;
  }

  &__logout {
    background-color: transparent;
    color: $white;
    font-weight: 400;
    margin-left: auto;
    font-size: 14px;
    padding: 10px;
    display: flex;
    align-items: center;
    opacity: 0.5;
    transition: opacity 500ms;
    border: 1px solid $white;
    border-radius: 5px;
    cursor: pointer;

    &:after {
      content: '';
      display: block;
      background-image: url(~@img/logout.svg);
      width: 20px;
      height: 20px;
      background-size: cover;
      margin-left: 15px;
    }

    &:hover {
      opacity: 0.75;
    }
  }
}

.chat-main {
  position: relative;

  &--cube {
    @include gradient-border($border-width: 5px, $outer-shadow: 0px 0px 20px rgba(255, 255, 255, 0.5));
    width: 420px;
    height: 420px;
    background: rgba(28, 28, 31, 0.25);

    &:before {
      animation: flickr 5000ms infinite forwards linear;
    }
  }

  &--cube &__inner {
    width: 100%;
    height: 100%;
    padding: 10px;
    overflow: auto;
    @include custom-scroll();
  }

  &__inner--overlay {
    position: absolute;
    width: calc(100% - 30px);
    height: calc(100% - 30px);
    top: 0;
    left: 0;
  }

  &__inner--hide {
    display: none;
  }

  &__inner &__login-guide,
  &__inner &__guest-list {
    height: 100%;
    width: 100%;
  }

  &__chat+&__chat {
    margin-top: 20px;
  }

  &--cube &__chat+&__chat {
    margin-top: 20px;
  }

  &__chat--other+&__chat:not(#{&}__chat--other),
  &__chat:not(#{&}__chat--other)+&__chat--other {
    margin-top: 40px;
  }





  &__chat {
    text-align: right;

    &--other {
      text-align: left;
    }
  }

  &__bubble-name {
    color: transparentize($white, 0.5);
    font-size: 14px;
    margin: 10px;
  }


  &__bubble {
    background: #bcbfc5;
    color: rgb(54, 54, 54);
    font-size: 15px;
    border-radius: 15px 15px 0px 15px;
    font-weight: 400;
    padding: 15px;
    max-width: 200px;
    width: 100%;
    text-align: left;
    display: inline-block;
    animation: bubble 500ms;
    transform-origin: 100% 0%;
    opacity: 0.75;
    transition: opacity 500ms;
    word-break: break-all;

    &:hover {
      opacity: 1;
    }
  }

  &--cube &__bubble {
    background: transparent;
    border: 1px solid $white;
    color: $white
  }

  &__chat--other &__bubble {
    background: rgb(84, 67, 102);
    color: #c4c4c4;
    border-radius: 15px 15px 15px 0px;
    font-size: 15px;
    margin-right: 0;
    transform-origin: 0% 0%;
  }


  &--cube &__chat--other &__bubble {
    background: rgba(84, 67, 102, 0.5);
    color: $white;
    border-radius: 15px 15px 15px 0px;
    font-size: 15px;
    margin-right: 0;
    transform-origin: 0% 0%;
  }
}